import React, { useState } from "react";
import "../css/MainComponents.css"; // Importar o CSS para o cabealho
import images from "../shared/Banner/import"; // Corrigir a importao se necessrio
import Slider from "react-slick"; // Importar o Slider
import "slick-carousel/slick/slick.css"; // Importar CSS do Slick
import "slick-carousel/slick/slick-theme.css"; // Importar tema do Slick
import DepoimentoScreen from '../screen/DepoimentoScreen';
import BlogLayout from "../screen/BlogLayout";
import TrabalheConosco from "../screen/TrabalheScreen";
import Contato from "../screen/ContatoScreen";

const MainComponents = () => {
  const [isExpanded, setIsExpanded] = useState(false); // Estado para controlar a expanso do texto

  const toggleReadMore = () => {
    setIsExpanded(!isExpanded); // Alterna o estado de expanso
  };

  const carouselSettings = {
    dots: true, // Mostrar pontos de navegao
    infinite: true, // Permitir looping
    speed: 500, // Velocidade da transio
    slidesToShow: 1, // Quantidade de slides mostrados
    slidesToScroll: 1, // Quantidade de slides a serem rolados por vez
    autoplay: true, // Ativar autoplay
    autoplaySpeed: 3000, // Tempo de espera entre as transies
    arrows: true, // Sem setas de navegao
    fade: true, // Ativar transio suave
  };

  const partnerCarouselSettings = {
    infinite: true, // Loop infinito
    speed: 5000, // Velocidade da rotao
    slidesToShow: 5, // Quantidade de empresas mostradas por vez
    slidesToScroll: 1, // Quantidade de slides a serem rolados por vez
    autoplay: true, // Autoplay ativado
    autoplaySpeed: 2000, // Troca de slide a cada 2 segundos
    arrows: false, // Sem setas de navegao
    cssEase: "linear", // Roda suavemente e continuamente
    pauseOnHover: false, // Continua rodando mesmo ao passar o mouse
  };

  const serviceCarouselSettings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 3, // Mude para 3 para mostrar 3 cards ao mesmo tempo
    slidesToScroll: 1,
    autoplay: false,
    arrows: true,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 2, // Exibir 2 cards em telas menores
          slidesToScroll: 1,
        },
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 1, // Exibir 1 card em telas ainda menores
          slidesToScroll: 1,
        },
      },
    ],
  };

  return (
    <main className="main">
      <Slider {...carouselSettings}>
        <div>
          <img
            src={images.bombeiro}
            alt="Bombeiro"
            className="responsive-image"
          />
        </div>
        <div>
          <img
            src={images.cozinha}
            alt="Cozinha"
            className="responsive-image"
          />
        </div>
        <div>
          <img
            src={images.cientista}
            alt="Cientista"
            className="responsive-image"
          />
        </div>
        <div>
          <img
            src={images.logistica}
            alt="Logstica"
            className="responsive-image"
          />
        </div>
        <div>
          <img src={images.medica} alt="Mdica" className="responsive-image" />
        </div>
      </Slider>

      {/* Seo Sobre Ns */}
      <section className="about-us">
        <div className="about-text">
          <h2>Por que nosso nome  Sinergia?</h2>
          <p>
            Na SinergiaRH, acreditamos que "sinergia"  um conceito central e
            vital para a nossa misso e prticas dirias, o que representa a
            combinao harmoniosa de elementos que, ao se unirem, produzem um
            efeito maior do que a soma das suas partes individuais. Aqui est
            como aplicamos esse conceito em nossas atividades:
          </p>
          <p>
            {isExpanded
              ? `Colaborao Eficiente: Trabalhamos em estreita colaborao com nossos clientes, entendendo suas
              necessidades e objetivos, para desenvolver solues de RH que realmente faam a diferena. Essa parceria
              resulta em processos mais eficientes e eficazes.

              Alinhamento Estratgico: Alinhamos as prticas de recursos humanos com os objetivos estratgicos das empresas,
              garantindo que todos os esforos estejam direcionados para o crescimento e sucesso organizacional.

              Engajamento e Motivao: Criamos estratgias que promovem o engajamento e a motivao dos colaboradores,
              contribuindo para um ambiente de trabalho positivo e produtivo.

              Desenvolvimento Integrado: Oferecemos programas de desenvolvimento que abrangem desde o recrutamento at a
              gesto de talentos, assegurando que todos os nveis da organizao estejam alinhados e comprometidos com os
              mesmos objetivos.

              Resultados Otimizados: A combinao de nossos servios - desde a implantao de cultura de RH, gesto de
              cargos e salrios, at terceirizao de mo de obra - resulta em uma abordagem holstica que otimiza os
              resultados para nossos clientes.`
              : `Colaborao Eficiente: Trabalhamos em estreita colaborao com nossos clientes, entendendo suas
              necessidades e objetivos, para desenvolver solues de RH que realmente faam a diferena. Essa parceria
              resulta em processos mais eficientes e eficazes.

              Alinhamento Estratgico: Alinhamos as prticas de recursos humanos com os objetivos estratgicos das empresas, 
              garantindo que todos os esforos estejam direcionados para o crescimento e sucesso organizacional. 
              ...`}{" "}
            {/* Mostrar texto cortado ou expandido */}
          </p>
          <button onClick={toggleReadMore} className="read-more-button">
            {isExpanded ? "Ler menos" : "Ler mais"}
          </button>
        </div>
        <div className="about-image">
          <img
            src={images.about}
            alt="Imagem representativa"
            className="responsive-image"
          />{" "}
          {/* Substitua suaImagem pela imagem que deseja mostrar */}
        </div>
      </section>

      {/* Seo de Apresentao dos Membros */}
      <section className="team-section">
        <h2>Nossa Equipe</h2>
        <div className="team-container">
          <div className="team-member">
            <img src={images.membro1} alt="Membro 1" className="team-photo" />
            <h3>Joo Silva</h3>
            <p>
              Especialista em Gesto de Pessoas com mais de 15 anos de
              experincia em Recursos Humanos e Desenvolvimento Organizacional.
            </p>
          </div>
          <div className="team-member">
            <img src={images.membro2} alt="Membro 2" className="team-photo" />
            <h3>Maria Oliveira</h3>
            <p>
              Consultora Snior em Estratgias de Negcios, com expertise em
              Planejamento Estratgico e Gesto Financeira.
            </p>
          </div>
          <div className="team-member">
            <img src={images.membro3} alt="Membro 3" className="team-photo" />
            <h3>Carlos Pereira</h3>
            <p>
              Profissional de Marketing com foco em Branding e Comunicao
              Corporativa, ajudando empresas a construir marcas fortes.
            </p>
          </div>
          {/* Adicione mais membros conforme necessrio */}
        </div>
      </section>

      {/* Carrossel de Empresas Parceiras */}
      <section className="partners-carousel">
        <h2>Empresas Parceiras</h2>
        <Slider {...partnerCarouselSettings}>
          <div>
            <img
              src={images.empresa1}
              alt="Parceiro 1"
              className="partner-image"
            />
          </div>
          <div>
            <img
              src={images.empresa2}
              alt="Parceiro 2"
              className="partner-image"
            />
          </div>
          <div>
            <img
              src={images.empresa3}
              alt="Parceiro 3"
              className="partner-image"
            />
          </div>
          <div>
            <img
              src={images.empresa4}
              alt="Parceiro 4"
              className="partner-image"
            />
          </div>
          <div>
            <img
              src={images.empresa5}
              alt="Parceiro 5"
              className="partner-image"
            />
          </div>
          <div>
            <img
              src={images.empresa6}
              alt="Parceiro 6"
              className="partner-image"
            />
          </div>
          {/* Adicione mais empresas parceiras conforme necessrio */}
        </Slider>
      </section>

      {/* Seo Nossos Valores e Nossa Misso */}
      <section className="values-mission-section">
        <div className="values-mission-container">
          {/* Seo Nossos Valores */}
          <div className="values-section">
            <h2>Nossos Valores</h2>
            <ul>
              <li>
                <strong>Comprometimento:</strong> Estamos empenhados em entregar
                solues de alta qualidade que superem as expectativas de nossos
                clientes e candidatos.
              </li>
              <li>
                <strong>Transparncia:</strong> Valorizamos a comunicao aberta
                e honesta, garantindo clareza em todas as etapas do processo.
              </li>
              <li>
                <strong>Inovao:</strong> Utilizamos as melhores prticas e
                tecnologias avanadas para otimizar nossos servios e oferecer
                uma experincia diferenciada.
              </li>
              <li>
                <strong>Parceria:</strong> Trabalhamos em estreita colaborao
                com nossos clientes e candidatos, entendendo suas necessidades e
                construindo relacionamentos duradouros.
              </li>
              <li>
                <strong>tica:</strong> Mantemos os mais altos padres ticos em
                todas as nossas aes, respeitando a confidencialidade e a
                integridade de todos os envolvidos.
              </li>
            </ul>
          </div>

          {/* Seo Nossa Misso */}
          <div className="mission-section">
            <h2>Nossa Misso</h2>
            <p>
              Nossa misso  prestar servios de Consultoria em RH de forma
              estratgica e alinhada s necessidades de nossos parceiros e
              clientes. Acreditamos que o capital humano  o maior diferencial
              competitivo e, por isso, dedicamos nossos esforos para garantir
              que cada contratao seja uma SINERGIA perfeita entre as
              expectativas das empresas e as aspiraes dos profissionais.
            </p>
          </div>
        </div>
      </section>

      {/* Carrossel de Servios */}
      {/* Carrossel de Servios */}
      <section className="services-section">
        <h2>Nossos Servios</h2>
        <Slider {...serviceCarouselSettings}>
          <div className="service-card">
            <h3>Processos Seletivos</h3>
            <p>
              Conduzimos processos de Recrutamento e Seleo de talentos para
              diversas reas e nveis hierrquicos.
            </p>
            <h4>Processo:</h4>
            <p>
              Aplicamos metodologias de entrevistas e avaliaes personalizadas.
            </p>
            <h4>Resultados Esperados:</h4>
            <p>Contrataes mais adequadas s necessidades das empresas.</p>
            <h4>Casos de Sucesso:</h4>
            <p>
              Aumentamos a reteno de talentos em 30% para nossos clientes.
            </p>
          </div>

          <div className="service-card">
            <h3>Implantao de Cultura de RH</h3>
            <p>
              Ajudamos a estabelecer e fortalecer a cultura organizacional,
              alinhando-a aos objetivos estratgicos da empresa.
            </p>
            <h4>Processo:</h4>
            <p>Realizamos workshops e treinamentos para todos os nveis.</p>
            <h4>Resultados Esperados:</h4>
            <p>Ambiente de trabalho positivo e colaborativo.</p>
            <h4>Casos de Sucesso:</h4>
            <p>
              Reduzimos a rotatividade de funcionrios em 25% em empresas
              parceiras.
            </p>
          </div>

          <div className="service-card">
            <h3>Gesto de Cargos e Salrios</h3>
            <p>
              Desenvolvemos e implementamos planos de cargos e salrios,
              garantindo equidade e competitividade no mercado.
            </p>
            <h4>Processo:</h4>
            <p>Analisamos o mercado e estabelecemos estruturas de cargos.</p>
            <h4>Resultados Esperados:</h4>
            <p>Equidade salarial e maior motivao dos colaboradores.</p>
            <h4>Casos de Sucesso:</h4>
            <p>Aumentamos a satisfao dos colaboradores em 40%.</p>
          </div>

          <div className="service-card">
            <h3>Desenvolvimento Organizacional</h3>
            <p>
              Promovemos iniciativas que visam o crescimento sustentvel da
              organizao, melhorando processos e estruturas.
            </p>
            <h4>Processo:</h4>
            <p>Avaliamos e otimizamos os processos internos.</p>
            <h4>Resultados Esperados:</h4>
            <p>Aumento da eficincia operacional e reduo de custos.</p>
            <h4>Casos de Sucesso:</h4>
            <p>
              Melhoria de 30% na eficincia operacional em empresas atendidas.
            </p>
          </div>

          <div className="service-card">
            <h3>Endomarketing</h3>
            <p>
              Criamos estratgias de comunicao interna para engajar e motivar
              colaboradores.
            </p>
            <h4>Processo:</h4>
            <p>Desenvolvemos campanhas internas e eventos motivacionais.</p>
            <h4>Resultados Esperados:</h4>
            <p>Aumento do engajamento e da satisfao dos colaboradores.</p>
            <h4>Casos de Sucesso:</h4>
            <p>Aumento do engajamento dos colaboradores em 35%.</p>
          </div>

          {/* Adicione mais cards conforme necessrio */}
        </Slider>
      </section>
      <DepoimentoScreen/>

      <BlogLayout/>

      <TrabalheConosco/>

      <Contato/>


     
    </main>
  );
};

export default MainComponents;
